.form_control{
  position: relative ;
}
.form_control.error input,
.form_control.error textarea{
  border: 2px solid red;
}
.form_control_input{
  position: relative ;
}
.form_control_input.error input,
.form_control_input.error textarea{
  border: 2px solid red;
}
.form_control.error span{
  color: white;
  position: absolute;
  top: -30px;
  right: 0px;
  background-color: red;
  border-radius: 5px;
  padding: 0 10px 3px;
  font-weight: 500;
}
.form_control_input.error span{
  color: white;
  position: absolute;
  top: 1px;
  right: -290px;
  background-color: red;
  border-radius: 5px;
  padding: 0 10px 3px;
  font-weight: 500;
}

.form_sumary_error{
  border: 2px solid red;
  padding: 5px;
  color: white;
  text-align: center;
  background-color: red;
  position: absolute;
  bottom: -35px;
  right: 0;
  left: 0;
}