.users{
  padding: 20px 10px 10px;
}
.add_users__btn{
  margin: 15px auto;
  width: 300px;
}
.add_users__btn button{
  color: lavender;
}
.user{
  display: flex;
  margin: 30px auto;
  background-color: darkslategrey;
  border-radius: 10px;
  width: 700px;
}
.ava_box{
  text-align: center;
  margin-right: 20px;
  padding-left: 10px;
  padding-bottom: 10px;
  min-width: 145px;
}
.ava{
  margin: 10px 0 5px;
}
.subscribe button{
  background-color: brown;
  cursor: pointer;
}
.subscribe button:disabled{
  background-color: darkgray;
  cursor: default;
}
.ava img{
  height: 100px;
  width: 100px;
  border-radius: 50%;
}
.context_box{
  display: flex;
  justify-content: space-between;
  margin: 20px 20px 20px 0;
  border-radius: 10px;
  padding: 5px 15px 10px;
  width: 100%;
  font-size: 18px;
  color: #fff;
  border: 2px solid blueviolet;
}
.context_boxi{
  display: flex;
  justify-content: space-between;
  margin: 20px 20px 20px 0;
  border-radius: 10px;
  padding: 5px 15px 10px;
  width: 100%;
  font-size: 18px;
  color: darkgray;
  border: 2px solid brown;
}
.context_left{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.context_right{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}
.subscribe .unfollow_btn{
  background-color: blueviolet;
}
.gif_loading{
  width: 70px;
  height: 70px;
  position: absolute;
  top: 5px;
  left: 300px;
  z-index: 2;
}
.paginacia_list button{
  position: absolute;
}