*,
:after,
:before{
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: inherit;
  color: lightgoldenrodyellow;
}
ul,
li{
  padding: 0;
  margin: 0;
  list-style: none;
}
.App {
  width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas: 
  "h h h"
  "n c c";
}
.App-wrapper__content{
    background-color: darkcyan;
    grid-area: c;
    position: relative;
}
textarea{
  resize: none;
  background-color: #eee;
  width: 500px;
  height: 100px;
  font-size: 18px;
  font-weight: 300;
  outline: none;
  border-radius: 10px;
  padding: 10px 20px 10px 20px;
  margin-bottom: 10px;
}
button{
  border: none;
  background-color: darkslategray;
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: 500;
  font-size: 17px;
  letter-spacing: 1px;
  cursor: pointer;
  color: gainsboro;
  outline: none;
}