.paginacia_list{
  display: flex;
  color: burlywood;
  position: relative;
  justify-content: center;
}
.paginacia_list li{
  margin: 7px;
  cursor: pointer;
  z-index: 2;
}
.active_pagination{
  font-weight: 700;
  color: #fff;
  cursor: default !important;
}
